@import "../../../styles/general.scss";

.contact-profile-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 0.6;
  background-color: $grey;
  @media screen and (max-width: 1100px) {
    // flex-direction: row;
    width: 100%;
    height: 45%;
    background-color: greenyellow;
    display: none;
  }
}

.contacts-profile {
  display: flex;
  flex-direction: column;
  width: 16.5em;
  height: 23em;
  border-radius: 5px;
  margin: 5em 0.5em 0em 0.5em;
  padding: 1em 2em 1em 2em;
  background-color: $white;
  //about label
  label {
    width: 18em;
    height: 7em;
    overflow-x: hidden;
  }
  //user name
  h2 {
    text-align: center;
    margin: 0em;
  }
  //about
  p {
    text-align: left;
    color: $darker-grey;
    font-size: 1em;
    margin: 0.5em 0em;
  }
}

.contact-profile-menu-wrap {
  display: flex;
  justify-content: space-evenly;
  height: 2.5em;
}

hr {
  border: 1px solid $grey;
  width: 16em;
  align-self: center;
  margin-top: 0.5em;
}
