@import "../../../styles/general.scss";

.me {
  display: flex;
  justify-content: flex-start;
  flex-grow: 0.2;
  margin: 0em;
  padding-left: 0.8em;
  width: 80%;
  div {
    display: flex;
  }
  //setting icon
  .setting-icon {
    cursor: pointer;
    margin: 3.6em 0em 0em 3.5em;
    width: 1.2em;
    height: 1.2em;
    @media screen and (max-width: 1100px) {
      margin-left: 50%;
    }
  }
}

.myIntroduce {
  display: flex;
  flex-direction: column;
  margin: 2.2em 0em 0em 0em;
  margin-left: 1em;
  label {
    width: 12em;
    height: 3.5em;
    overflow-x: hidden;
    margin-top: 0.2em;
  }
  //user name
  h2 {
    margin: 0em;
    text-align: left;
    max-width: 9em;
    height: 1.8em;
    overflow: auto;
  }
  //about
  p {
    text-align: left;
    font-size: 1em;
    margin: 0em;
    color: $darker-grey;
  }
}
